<template lang="pug">
//- 2a Usuaria no logueada
    2b Usuaria registrada no logueada
.step
  .layout-inner
    form.form(
      v-on:submit='',
      action='#',
      submit.prevent='validateBeforeSubmit',
      method='post')
      fieldset.form-section
        legend.subhead.form-section__title ¬°Est√°s a un paso de publicar tu venta!
          p.form-section__subtitle Para publicar tu venta, ingresa tu correo
        .form-section__inner
          .form__row(
            v-bind:class='{ "is-danger": errorTexts.email }')
            label.form__label(
              for='emailLogin') Correo
            span.help(
              v-if="errorTexts.email") {{ errorTexts.email }}
            input.form__control(
              v-model='email',
              id='emailLogin',
              type='email')
          .form__row_top
            p.form__mesagge Vemos que ya tienes una cuenta, por favor ingresa tu contrase√±a
            .form__row(
              v-bind:class='{ "is-danger": errorTexts.password }')
              label.form__label(
                for='passwordLogin') Contrase√±a
              span.help(
                v-if="errorTexts.password"
              ) {{ errorTexts.password }}
              input.form__control(
                v-model='password',
                id='passwordLogin',
                type='password')
              p.form__note.form__note_right
                |¬øOlvidaste tu contrase√±a?
                | <a class='link_underline' href='#/password' title='Ir a recuperar contrase√±a'>Recuperar contrase√±a.</a>
          .form-section.form-section_footer
            .form__row.form__row_away
              button.btn.btn_solid(
                @click.prevent='validateBeforeSubmit') Continuar
</template>

<script>
import axios from 'axios'
export default {
  name: 'FormPublicarLogin',
  data () {
    return {
      email: '',
      password: '',
      errorTexts: {}
    }
  },
  methods: {
    validateBeforeSubmit: function () {
      this.errorTexts = {}

      if (!this.email) {
        this.errorTexts.email = 'Debes ingresar tu email'
      } else {
        if (!/^(?:[\w\!\#\$\%\&\'\*\+\-\/\=\?\^\`\{\|\}\~]+\.)*[\w\!\#\$\%\&\'\*\+\-\/\=\?\^\`\{\|\}\~]+@(?:(?:(?:[a-zA-Z0-9](?:[a-zA-Z0-9\-](?!\.)){0,61}[a-zA-Z0-9]?\.)+[a-zA-Z0-9](?:[a-zA-Z0-9\-](?!$)){0,61}[a-zA-Z0-9]?)|(?:\[(?:(?:[01]?\d{1,2}|2[0-4]\d|25[0-5])\.){3}(?:[01]?\d{1,2}|2[0-4]\d|25[0-5])\]))$/.test(this.email)) {
          this.errorTexts.email = 'El email que ingresaste no parece v√°lido.'
        }
      }

      if (!this.password) this.errorTexts.password = 'Debes ingresar una contrase√±a'

      if (Object.keys(this.errorTexts).length === 0) {
        this.login()
      }
    },
    login: function () {
      axios.post('https://prilov.aguayo.co/api/users/login', {
        email: this.email,
        password: this.password
      })
        .then(response => {
          console.log(response.data)
          localStorage.setItem('token', response.data.api_token)
          localStorage.setItem('userId', response.data.id)

          this.$store.dispatch('user/setUser', response.data)
          this.close()
        })
        .catch(e => {
          console.log('ERROR : ' + e)
        })
    }
  }
}
</script>



